var app = getApp();
const commonn = require('../common/js/common.js');

export default {
	data() {
		return {
			host: app.globalData.host, //全局变量值的获取
			oosHost: app.globalData.oosHost,
			bannerUrl: app.globalData.oosHost + 'sys/index_banner.jpg',
			navArray: [
				{
					pageUrl: '/pages/universityActivities/activityIndex/activityIndex',
					imgUrl: 'http://mumu-small.oss-cn-shenzhen.aliyuncs.com/img/index/icon_active.png',
					text: '大学活动'
				},
				{
					pageUrl: '',
					imgUrl: 'http://mumu-small.oss-cn-shenzhen.aliyuncs.com/img/index/icon_team.png',
					text: '组队招募'
				},
				{
					pageUrl: '',
					imgUrl: 'http://mumu-small.oss-cn-shenzhen.aliyuncs.com/img/index/icon_reward.png',
					text: '敬请期待'
				}
			],
			graphics: []
		};
	},
	onLoad() {
		/**判断是否登录 */
		wx.getStorage({
			key: 'token',
			success: function(res) {
				//成功获取本地token
			},
			fail: function(res) {
				console.log('错误');
				console.log(res);
				// wx.redirectTo({
				//   url: '/pages/home/login/login',
				//   fail: function (res) {
				//     //未跳转
				//   }
				// })
			}
		});
		/**获取首页精选图文 */
		this.getHotGraphic();
		// var that = this;
		// that.getHomeData();
		// console.log(that.data.host)
		wx.loadFontFace({
			family: 'PangMenZhengDao',
			source: 'url("https://cmrs-oos.oss-cn-beijing.aliyuncs.com/font/pmzd.ttf")'
		});
	},
	methods: {
		/**
		 * 获取热门图文
		 */
		getHotGraphic: function() {
			commonn
				.requestURL({
					url: '/graphic/selection',
					method: 'GET'
				})
				.then(res => {
					this.setData({
						graphics: res.data
					});
				});
		}
		//获取主页的后端数据
		// getHomeData: function () {
		//   var that = this;
		//   wx.request({
		//     url: that.data.host + "/activity/view", //小程序目前发起request请求，必须是https协议
		//     success: function (res) {
		//       console.log(res);
		//     },
		//     fail: function (res) {
		//       console.log(res)
		//     }
		//   })
		// }
	}
};
